/**
 * DeviceLayoutRenderer.tsx - Enterprise Device Layout Orchestrator
 *
 * Single source of truth Œ≥ŒπŒ± device-specific rendering œÉœÑŒø Layera ecosystem.
 * ŒëŒΩœÑŒπŒ∫Œ±Œ∏ŒπœÉœÑŒ¨ duplicate device rendering logic ŒºŒµ unified API.
 */

import React from 'react';
import { DeviceLayoutRendererProps, ResponsiveLayoutConfig, DeviceType } from './types';

const DEFAULT_LAYOUT_CONFIG: ResponsiveLayoutConfig = {
  iphone: {
    width: 430,
    height: 932,
    containerStyle: {
      position: 'relative',
      overflow: 'hidden'
    }
  },
  tablet: {
    containerStyle: {
      width: '100%',
      height: '100vh',
      position: 'relative'
    },
    containerClassName: 'tablet-map-container'
  },
  desktop: {
    containerStyle: {
      width: '100%',
      height: '100vh',
      position: 'relative'
    }
  },
  mobile: {
    containerStyle: {
      width: '100%',
      height: '100vh',
      position: 'relative'
    },
    containerClassName: 'mobile-map-container'
  }
};

export const DeviceLayoutRenderer: React.FC<DeviceLayoutRendererProps> = ({
  deviceType: propDeviceType,
  forceDeviceType,
  layoutConfig = {},
  commonProps = {},
  components = {} as DeviceLayoutRendererProps['components'],
  navigation,
  navigationHandlers,
  showCategoryElements = false,
  fab
}) => {
  // Auto-detect device type Œ±ŒΩ Œ¥ŒµŒΩ Œ¥ŒØŒΩŒµœÑŒ±Œπ - STABILIZED
  const detectedDeviceType = React.useMemo((): DeviceType => {
    if (forceDeviceType) {
      return forceDeviceType;
    }

    if (propDeviceType) {
      return propDeviceType;
    }

    // Fallback auto-detection (Œ∏Œ± ŒºœÄŒøœÅŒøœçœÉŒµ ŒΩŒ± œáœÅŒ∑œÉŒπŒºŒøœÄŒøŒπŒÆœÉŒµŒπ @layera/device-detection)
    if (typeof window === 'undefined') {
      return 'desktop';
    }

    // ŒßœÅŒ∑œÉŒπŒºŒøœÄŒøŒπœé œÉœÑŒ±Œ∏ŒµœÅŒÆ œÑŒπŒºŒÆ Œ≥ŒπŒ± ŒΩŒ± Œ±œÄŒøœÜœçŒ≥œâ infinite re-renders
    const width = window.innerWidth;
    if (width <= 430) {
      return 'mobile';
    } else if (width <= 768) {
      return 'tablet';
    } else {
      return 'desktop';
    }
  }, [propDeviceType, forceDeviceType]);

  // Merge configuration
  const finalConfig = {
    ...DEFAULT_LAYOUT_CONFIG,
    ...layoutConfig
  };

  // iPhone rendering
  // Œ£ŒóŒúŒëŒùŒ§ŒôŒöŒü: ŒëœÜŒ±ŒπœÅŒ≠Œ∏Œ∑Œ∫Œ±ŒΩ œÑŒ± console.log Œ≥ŒπŒ± Œ±œÄŒøœÜœÖŒ≥ŒÆ infinite render loops
  // œÄŒøœÖ œÄœÅŒøŒ∫Œ±ŒªŒøœçœÉŒ±ŒΩ ŒµŒæŒ±œÜŒ¨ŒΩŒπœÉŒ∑ œÑŒøœÖ FAB œÉœÑŒø iPhone 14 Pro Max
  console.log('üîç Debug DeviceLayoutRenderer:', { detectedDeviceType, showCategoryElements });

  if (detectedDeviceType === 'iphone') {
    const config = finalConfig.iphone;
    const iPhoneComponents = components?.iphone || {};

    console.log('üîç Debug iPhone components:', {
      hasStepper: !!iPhoneComponents.stepper,
      hasCategory: !!iPhoneComponents.category,
      showCategoryElements
    });

    return (
      <div
        style={{
          width: config.width,
          height: config.height,
          ...config.containerStyle
        }}
      >
        {/* Main device component */}
        {iPhoneComponents.map && React.createElement(iPhoneComponents.map, {
          ...commonProps,
          isIPhone14ProMaxDevice: true
        })}

        {/* Conditional stepper rendering */}
        {showCategoryElements && iPhoneComponents.stepper && navigation && (
          React.createElement(iPhoneComponents.stepper, {
            currentStep: navigation.currentStep,
            totalSteps: navigation.totalSteps,
            stepIndex: navigation.stepIndex,
            selectedCategory: navigation.selectedCategory,
            onNext: navigationHandlers?.onNext,
            onPrevious: navigationHandlers?.onPrevious,
            onReset: navigationHandlers?.onReset,
            onStepClick: navigationHandlers?.onStepClick,
            canGoNext: navigation.canGoNext,
            canGoPrevious: navigation.canGoBack
          })
        )}

        {/* Conditional category rendering */}
        {showCategoryElements && (() => {
          console.log('üîç Debug CategoryStep rendering:', {
            showCategoryElements,
            hasCategory: !!iPhoneComponents.category,
            hasNavigation: !!navigation,
            currentStep: navigation?.currentStep
          });

          if (iPhoneComponents.category && navigation) {
            return React.createElement(iPhoneComponents.category, {
              isVisible: true, // Force visible for debugging
              currentStepId: navigation.currentStep,
              onNext: async (_category: unknown) => {
                // Use proper navigation handler Œ≥ŒπŒ± category selection
                try {
                  if (navigationHandlers?.onNext) {
                    // Category selection ŒºŒ≠œÉœâ navigation handler
                    navigationHandlers.onNext();
                  }
                } catch (error) {
                  // Error handling œáœâœÅŒØœÇ console output Œ≥ŒπŒ± clean logs
                }
              }
            });
          }
          return null;
        })()}

      </div>
    );
  }

  // Desktop rendering
  if (detectedDeviceType === 'desktop') {
    const config = finalConfig.desktop;
    const desktopComponents = components?.desktop || {};

    return (
      <div style={config.containerStyle}>
        {/* Desktop-specific component */}
        {desktopComponents.map && React.createElement(desktopComponents.map, commonProps)}

        {/* Common MapContainer component */}
        {commonProps && (
          <div>
            {/* MapContainer Œ∏Œ± rendered ŒµŒ¥œé */}
          </div>
        )}

      </div>
    );
  }

  // Tablet rendering
  if (detectedDeviceType === 'tablet') {
    const config = finalConfig.tablet;
    const tabletComponents = components?.tablet || {};

    return (
      <div
        className={config.containerClassName}
        style={config.containerStyle}
      >
        {/* Tablet-specific component */}
        {tabletComponents.map && React.createElement(tabletComponents.map, commonProps)}

        {/* Common MapContainer component */}
        {commonProps && (
          <div>
            {/* MapContainer Œ∏Œ± rendered ŒµŒ¥œé */}
          </div>
        )}

      </div>
    );
  }

  // Mobile fallback rendering
  const config = finalConfig.mobile;
  return (
    <div
      className={config.containerClassName}
      style={config.containerStyle}
    >
      {/* Common MapContainer component */}
      {commonProps && (
        <div>
          {/* MapContainer Œ∏Œ± rendered ŒµŒ¥œé */}
        </div>
      )}

      {/* FAB rendering */}
      {fab && !fab.hidden && fab.component}
    </div>
  );
};